$colors: (
color:purple,
paragraph:red,
x: none,
);
div.class-o{color: map-get($colors,color);}
div.class-o p{color: map-get($colors,paragraph);
	-webkit-user-select: map-get($colors, x);
	   -moz-user-select: map-get($colors, x);
	    -ms-user-select: map-get($colors, x);
	        user-select: map-get($colors, x);}
$colors: (
color:silver,
paragraph:cornflowerblue,
);
div.class-t{color: map-get($colors,color);}
div.class-t p{color: map-get($colors,paragraph);-webkit-user-select: none;-moz-user-select: none;-ms-user-select: none;user-select: none;}